<template>
  <div class="footer">
        <div class="cart-details">
            <p>Total Item Types: <span class="cart-nums">{{cartItems.length}}</span></p>
            <p>Total Items: <span class="cart-nums">{{cartItemsTotalNum}}</span></p>            
        </div>
        <div class="checkout">
            <p>Total Price: <span class="cart-nums">${{cartItemsTotal}}</span></p>
            <button class="checkout-btn" @click="handleCheckout">Checkout</button>
        </div>
        
  </div>
  
</template>
<script>
export default {
  name: "Footer",
  computed: {
    cartItems() {
      return this.$store.state.cart;
    },
    cartItemsTotal() {
      let items = this.$store.state.cart;
      let total = 0;
      items.forEach(item => {
        total += item.totalPrice;
      });
      return total.toFixed(2);
    },
    cartItemsTotalNum(){
      let items = this.$store.state.cart;
      let total = 0;
      items.forEach(item => {
        total += item.qtyAdded;
      });
      return total;
    }
  },
  methods: {
    handleCheckout() {
      if (this.$store.state.cart.length > 0) {
        this.$store.commit("setCheckoutModalStatus", true);
      } else {
        alert("Cart is empty");
      }
    }
  },
  components: {}
};
</script>
<style>
.footer {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  height: 100px;
  background-color: #e2e2e2;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin-top: 10px;
  padding: 5px 20px 20px 20px;
}
.cart-details {
  grid-column: span 8;
  display:block;
}
.checkout {
  grid-column: span 4;
  display:block;
  text-align:right;
}
.checkout-btn {
  background-color: rgb(31, 168, 99); 
  border: none;
  color: white;
  padding: 10px 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  /* border-radius: 12px; */
  /* margin-top: 20px; */
}
.cart-nums{
  background-color:#db5757;
  padding:0px 10px 0px 10px;
  color:white;
  font-size:18px;
}
</style>